html,
body {
  height: 100vh;
  width: 100vw;
  overflow-x: hidden;
  /* overflow-y: hidden; */
}
.dropdown-pull-right {
  float: right !important;
  right: 0 !important;
  left: auto !important;
}
.calendar-screen {
  display: flex;
  flex-flow: column;
  height: 100vh;
}
.rbc-calendar {
  height: 100%;
}

/*modal*/
.ReactModalPortal > div {
  opacity: 0;
}

.ReactModalPortal .ReactModal__Overlay {
  align-items: center;
  display: flex;
  justify-content: center;
  transition: opacity 0.2s ease-in-out;
  z-index: 999;
}

.modal-fondo {
  background-color: rgba(0, 0, 0, 0.3);
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  position: fixed;
}

.ReactModalPortal .ReactModal__Overlay--after-open {
  opacity: 1;
}

.ReactModalPortal .ReactModal__Overlay--before-close {
  opacity: 0;
}

.modal {
  background: white;
  border-radius: 5px;
  color: rgb(51, 51, 51);
  display: inline;
  max-height: 620px;
  max-width: 500px;
  outline: none;
  padding: 10px;
}
.black{
  color:rgb(51, 51, 51);
}
/*DatePicker*/

.react-datetime-picker input:focus {
  outline: none;
}
.react-datetime-picker__wrapper {
  border: 0px solid gray !important;
}
/*FABS*/
.fab {
  border-radius: 100%;
  padding: 25px;
  font-size: 30px;
  bottom: 25px;
  right: 25px;
  position:fixed;
}
.fab-danger {
  padding: 10px;
  bottom: 25px;
  left: 25px;
  position:fixed;
}
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
